@import url('https://fonts.googleapis.com/css?family=Architects+Daughter&display=swap');
* {
  margin: 0;
  padding: 0;
  outline: 0;
}

body {
  background-color: #FFEAA7;
  font-family: "Architects Daughter", sans-serif;
  text-align: center;
}

.todolist {
  margin: 50px auto;
  padding: 16px;
  min-height: 580px;
  max-width: 300px;
  background-color: #f1f5f8;
  background-image: radial-gradient(#bfc0c1 7.2%, transparent 0);
  background-size: 40px 40px;
  border-radius: 20px;
  box-shadow: 4px 3px 7px 2px #00000040;
}

.heading {
  display: flex;
  align-items: center;
  justify-content: center;
}

.heading h1 {
  width: 60%;
  margin-top: 30px;
  background-color: #fdc66e;
  border-radius: 10px;
  border-radius: 20% 5% 20% 5%/5% 20% 25% 20%;
  padding: 0.2rem 1.2rem;
  color: #494a4b;
}

.todolist form {
  margin-top: 40px;
}

.todolist input {  
  border: 0;
  border-bottom: 3px dashed #fdcb6e;
  background-color: transparent;
  font-family: "Architects Daughter", sans-serif;
  font-size: 1.3rem;
  padding: 10px 15px;
  width: 70%;
  color: #494a4b;
}

.todolist button {
  margin-top: 30px;
  border: 3px solid hsl(198, 1%, 29%);
  border-radius: 8px;
  padding: 10px 15px;
  font-family: "Architects Daughter", sans-serif;
  font-size: 0.9rem;
}



.items ul li {
  text-align: left;   
  font-size: 1.4rem;
  font-weight: 300;
  color: #494a4b;
  display: relative;
  
}